.bigContainer {
   padding: 0 10px;
   display: flex;
   flex-wrap: wrap;
   flex-direction: column;
   /* align-items: ; */
   justify-content: space-evenly;
}

.movieListContainer {
    padding: 0 50px;
    display: flex;
    flex-wrap: wrap;
    /* flex-direction: column; */
    align-items: center;
    justify-content: space-evenly;
}

.mainContainer {
    display: flex;
    flex-wrap: wrap;
 }

 .singleMovie {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    width: 250px;
    height: 390px;
 }

 .poster {
    width: 200px;
    height: 280px;
 }

 /* button stuff */

 .topnav {
   overflow: hidden;
   background-color: #e9e9e9;
 }
 
 .topnav a {
   float: left;
   padding: 14px 16px;
 }
 
 .topnav input[type=text] {
   padding: 6px;
   font-size: 17px;
   border: none;
   width: 80vw;
 }
 
 .topnav .search-container button {
   padding: 6px 10px;
   margin-right: 16px;
   background: #ddd;
   font-size: 17px;
   border: none;
   cursor: pointer;
 }